$teal:#25CAD3;
$teal-light:#5BD7DE;
$teal-lightest:#C8F2F4;

/* Greys */
$grey-darkest:#2B2B2B;
$grey-dark: #4f4f4f;
$grey: #808080;
$grey-light: #dadada;
$grey-lightest: #f1f1f1;



